import rs from '../../static/js/request.js';
export default {
	props: {
		keyStatu: {
			type: Boolean,
			value: false
		},
		arrObj: {
			type: Object,
			value: false
		},
		invest:{
			type:Boolean,
			value:false
		}
	},
	data() {
		return {
			val: '',
			id: 0
		};
	},
	methods: {
		clean() {
			this.val = '';
		},
		cancel() {
			this.val = '';
			this.$emit('cancelKey');
		},
		changeNum(data) {
			let str = String(data);
			if (data == 'delete') {
				this.val = this.val.substring(0, this.val.length - 1);
			} else {
				if (this.val.indexOf('.') >= 0 && data == '.') {
					rs.Toast('格式不正确');
					return;
				}
				this.val += str;
			}
		},
		toParent() {
			if (this.val == '') {
				rs.Toast('还没有输入');
			} else if (this.val == 0) {
				rs.Toast( '购买数量不能为零');
			} else if (this.arrObj.is_float == 1 && !Number.isInteger(Number(this.val))) {
				rs.Toast( '购买数量不能为小数');
			} else {
				var obj = {
					arrObj: this.arrObj,
					val: parseFloat(this.val)
				};
				this.$emit('keyBoard', this.keyStatu);
				this.$emit('toParent', obj);
				this.val = '';
			}
		}
	}
};
